:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }

.imageHolder {
  grid-area: two;
}

.image {
  height: 3em;
  width: 3em;
  margin-left: -0.33em;
}

.primary {
  grid-area: one;
}

@media (min-width: 1000px), print {

.primary {
    white-space: nowrap;
    text-align: right
}
  }

.primary__methods {
    margin-bottom: var(--rhythm__vertical);
  }

.name {
  padding-top: 0.5em;
  font-size: var(--header-size);
}

.address {
  grid-area: two;
}

.addressLine {
  display: block;
}
