/*@use postcss-cssnext;*/

html {
    box-sizing: border-box;
    
}

*, *:before, *:after {
box-sizing: inherit;
}

main {
    display: block;
}

ul, 
li, 
dl, 
dd,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
}

ul, ol {
    list-style: none;
    padding-left: 0;
  }

:link:not(:hover) {
    text-decoration: none;
}

:root {
    --header-size: 1.6rem;
    --header-margin-top: 0.15rem;
    --line-height__body: 1.35rem;
    --line-height__header: 2.7rem;
    --column: auto;
    --column-gap: 3em;
    --font__info: 'Theinhardt', sans-serif;
    --font__header: 'Theinhardt', sans-serif;
    --color__body: rgba(0,0,0,0.9);
    --color__subtle-off-white: #fafcfa;
    --color__ice: #4db0c4;
    --color__fire: #c47333;
    --color__prince: #c53fa6;
    --section-decoration: 0.075rem solid var(--color__ice);
    --rhythm__vertical: 1.35rem;
  }

@keyframes flip-out {
    0% {
      transform: rotateX(0deg);
    }
    100% {
      transform: rotateX(90deg);
      opacity: 0.4;
    }
  }

@keyframes flip-in {
    0% {
      transform: rotateX(-90deg);
      opacity: 0.4;
    }
    100% {
      transform: rotateX(0deg);
    }
}

@keyframes spin-out {
    0% {
      transform: rotateY(0deg);
    }
    100% {
      transform: rotateY(90deg);
      opacity: 0.4;
    }
  }

@keyframes spin-in {
    0% {
      transform: 
        rotateY(90deg) 
        translateX(50%) 
        rotateY(-90deg)
        translateX(50%)
        rotateY(90deg)
    }
    100% {
      transform: rotateY(0deg);
      transform-origin: 0 25%;
    }
}

@keyframes spinner {
    to {
        transform: rotate(-1turn);
    }
}

@font-face {
  font-family: "Calluna";
  src: url("../fonts/Calluna-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Theinhardt";
  src: url("../fonts/TheinhardtReg.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Theinhardt";
  src: url("../fonts/TheinhardtLight_alt.woff") format("woff");
  font-weight: lighter;
  font-style: normal;
}

:link {
  color: var(--color__ice);
}

:visited {
  color: var(--color__prince);
}

@media screen {

html {
    background: #888;
    font-size: 17px
}
  }

@media print {

html {
    font-size: 8pt
}
  }

body {
  font-family: "Theinhardt", sans-serif;
  line-height: var(--line-height__body);
  padding: 0;
  color: var(--color__body);
}

@media (min-width: 1000px) {

body {
    margin: 1em auto
}
  }

.cv {
  max-width: 1200px;
  margin: auto;
}

.cv__grid {
    margin: 0;
    background: var(--color__subtle-off-white);
    padding: 4vw;
  }

@media (min-width: 1000px) {

.cv__grid {
      padding: 4em
  }
    }

@media print {

.cv__grid {
      width: 21cm;
      padding: 0.5cm 2cm;
      max-width: none
  }
    }

.download-pdf {
  color: white;
  text-align: center;
}

@media print {

.download-pdf {
    display: none
}
  }

@media (min-width: 1000px) {

.download-pdf {
    position: fixed;
    z-index: 9999;
    transform: rotate(45deg);
    top: 2rem;
    right: -7rem
}
  }

.download-pdf__link {
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    padding: 1rem 7em;
    display: block;
    width: 100%;
    color: inherit;
    text-decoration: none;
    background: var(--color__fire);
  }

.download-pdf__link:hover {
      background: var(--color__ice);
    }

@media (min-width: 1000px) {

.download-pdf__text {
      display: inline-block;
      vertical-align: middle;
      width: 6em
  }
    }

.download-pdf__icon { /* from icono font */
    border: 2px solid;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    font-style: normal;
    color: inherit;;
    text-align: left;
    text-indent: -9999px;
    direction: ltr;
    width: 26px;
    height: 32px;
    border-radius: 0 0 0 10px;
    margin: 1px 4px 1px 0.5em;
  }

.download-pdf__icon::before,
    .download-pdf__icon::after {
      content: '';
      pointer-events: none;
    }

.download-pdf__icon::before {
      position: absolute;
      width: 0;
      height: 0;
      left: -3px;
      bottom: -3px;
      border-width: 5px;
      border-style: solid;
      border-bottom-color: transparent;
      border-left-color: transparent;
    }

.download-pdf__icon::after {
        width: 13px;
        height: 2px;
        box-shadow: inset 0 0 0 32px,0 -5px 0 0,0 5px 0 0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

.education__institution,
 .education__area {
  margin-bottom: 1.35rem;
 }

.cube {
    display: flex;
  }

@media (min-width: 1000px) {
      .cube--loading {
          transform-style: preserve-3d;
          animation: spin-in 1s backwards 1 1s;
          will-change: transform;
        }
      .cube__perspective {
        perspective: 1000px;
        transform-style: preserve-3d;
        transform-origin: top;
      }
    }

.cube__face-1 {
      backface-visibility: hidden;
      background: var(--color__fire);
      transform:  rotateY(-90deg) translateX(-100%);
      transform-origin: left;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-around;
    }

@media (max-width: 1000px) {

.cube__face-1 {
        display: none
    }
      }

.cube--loading .cube__face-1::before {
          content: "";
          height: 15vw;
          width: 15vw;
          margin-top: 33vh;
          border-radius: 50%;
          border: 1vw dotted white;
          border-bottom: 0 none;
          animation: spinner 3s backwards infinite linear;
        }

.cube__face-2 {
      backface-visibility: hidden;
    }

.cube-loaded .cube__face-2 {
        display: none;
      }

.grid {
  margin-bottom: calc(var(--rhythm__vertical) * 2);
}

.grid--tight-bottom {
    margin-bottom: 0;
  }

/* background-position-y: -6px;
  background-image: linear-gradient(#ddd 0, #ddd 1px, transparent 1px);
  background-size: 21.55px 21.55px; */

@media (min-width: 1000px),
  print {

.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: var(--column-gap);
    grid-template-areas: "one two-three two-three two-three"
}
  }

.grid__header {
    grid-area: one;
  }

.grid__main {
    grid-area: two-three
  }

@media (min-width: 600px),
    print  {

.grid__sub {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: var(--column-gap);
      grid-template-areas:
        "two three three"
  }
    }

.grid__sub-info {
    grid-area: two;
    margin-bottom: var(--rhythm__vertical); 
  }

.grid__sub-description {
    grid-area: three
  }

.technology {
    grid-template-areas:
    "one two-three two-three two-three";
  }

/* margin-bottom: var(--rhythm__vertical); */

.technology__list {
      grid-area: two-three;
      column-count: 2;
      column-gap: var(--column-gap);
      position: relative;
    }

@media (min-width: 600px),
      print {

.technology__list {
        column-count: 3
    }
      }

/* toDo: Make DRY */

.technology__list::before { /* toDo: Make DRY */
        content: ""; 
        position: absolute;
        left: 0;
        top: -0.45rem;
        width: 100%;
        border-top: var(--section-decoration);
      }

.technology__item {
      list-style: outside;
      color: var(--color__fire);
    }

.technology__item-text {
      color: var(--color__body);
    }

.role {
    margin-bottom: var(--rhythm__vertical);
  }

.role__title {
      margin-bottom: 1.35rem;
    }

.role__name:not(:last-child){ /* make more BEM? */
        margin-bottom: 1.35rem;
      }

@media (max-width: 1000px) {

.role__website {
        margin-bottom: 1.35rem
    }
      }

.role__header {
      margin-bottom: 0;
    }

.section-header {
  font-size: var(--header-size);
  font-family: var(--font__header);
  font-weight: lighter;
  white-space: nowrap;
  margin-top: -var(--header-margin-top);
  padding-bottom: calc(var(--line-height__body) + var(--header-margin-top));
}

@media (min-width: 1000px), print {

.section-header {
    margin-top: var(--header-margin-top);
    text-align: right
}
  }

.mkd {

  font-family: "Calluna", serif;
}

.mkd :root --list {
      /* depreceated, change when better standard created */
      font-size: 105%;
      margin: 0 0 var(--rhythm__vertical);
      padding-top: var(--rhythm__vertical);
    }

.mkd--p {
    font-size: 105%;
  }

.mkd--p + .mkd--p:not(:first-of-type) {
      text-indent: 1em;
    }

.mkd--ol {
    list-style: decimal;
    @apply --list;
  }

.mkd--ul {
    list-style: disc;
    @apply --list;
  }

.mkd--li {
    margin: 0;
  }

.divider {
  position: relative;
}

.divider::before {
    content: "";
    position: absolute;
    left: 0;
    top: -0.45rem;
    width: 100%;
    border-top: var(--section-decoration);
  }

@page {
  width: 21cm;
  margin: 0.5cm;
}
